// src/components/ReservasForm.tsx
"use client";

import { useState, useEffect, FormEvent } from "react";
import { useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import { Container, Form, Button, Alert, Row, Col } from "react-bootstrap";
import { loadStripe } from "@stripe/stripe-js";

const INSCRIPTION_PRICE_ID = "price_1RMB7pFV5ZpZiouCoDbA0KnV";
const servicios = [
  { slug: "agua", label: "Estimulaci√≥n en agua", priceId: INSCRIPTION_PRICE_ID },
  { slug: "piso", label: "Estimulaci√≥n en piso", priceId: "price_1RJd1jFV5ZpZiouC1xXvllVc" },
  /* ‚Ä¶ resto de servicios con su priceId ‚Ä¶ */
];
const terapeutas = ["Jes√∫s Ram√≠rez","Miguel Ram√≠rez","Alitzel Pacheco","Francia","Gisela"];

export default function ReservasForm() {
  const { data: session, status } = useSession();
  const router = useRouter();

  const [servicio, setServicio] = useState<string>("");
  const [terapeuta, setTerapeuta] = useState<string>("");
  const [fecha, setFecha] = useState<Date | null>(null);
  const [horario, setHorario] = useState<number | null>(null);
  const [hasInscription, setHasInscription] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);

  // 1) Si no est√° logueado, redirige
  useEffect(() => {
    if (status === "unauthenticated") router.replace("/login");
  }, [status, router]);

  // 2) Comprueba si ya pag√≥ inscripci√≥n
  useEffect(() => {
    if (session) {
      fetch("/api/user/has-inscription")
        .then(r => r.json())
        .then(data => setHasInscription(data.hasInscription))
        .catch(() => setHasInscription(false));
    }
  }, [session]);

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    if (!servicio || !terapeuta || !fecha || horario === null) {
      setError("Completa todos los campos");
      return;
    }

    // Si es agua y NO tiene inscripci√≥n: vamos a pagar inscripci√≥n
    if (servicio === "agua" && !hasInscription) {
      return router.push("/dashboard?tab=mis-paquetes");
    }

    try {
      // 3) Guardar reserva en tu historial
      await fetch("/api/appointments/create", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          servicio,
          terapeuta,
          date: fecha.toISOString(),
          hour: `${horario}:00`,
        }),
      });

      // 4) Iniciar checkout Stripe
      const svc = servicios.find(s => s.slug === servicio)!;
      const { sessionId, error: stripeError } = await fetch("/api/stripe/checkout", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          userId: session!.user!.id,
          lineItems: [{ price: svc.priceId, quantity: 1 }],
          metadata: { servicio, terapeuta, date: fecha.toISOString(), hour: `${horario}:00` },
        }),
      }).then(r => r.json());
      if (stripeError) throw new Error(stripeError);

      const stripe = await loadStripe(process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY!);
      await stripe!.redirectToCheckout({ sessionId });
    } catch (err: any) {
      console.error(err);
      setError(err.message || "Error al iniciar pago");
    }
  };

  // Horarios
  const horasSemana = Array.from({ length: 9 }, (_, i) => 10 + i);
  const horasSabado = Array.from({ length: 6 }, (_, i) => 9 + i);

  if (status === "loading" || !session) {
    return <Container className="py-5">Cargando‚Ä¶</Container>;
  }

  return (
    <Container className="py-5 dashboard-container">
      <h2 className="dashboard-header">Reservar cita</h2>
      {error && <Alert variant="danger">{error}</Alert>}

      <Form onSubmit={handleSubmit}>
        {/* ‚Ä¶ aqu√≠ van tus selects y Calendar ‚Ä¶ */}

        <Button type="submit" className="w-100 btn-orange">
          {servicio === "agua" && !hasInscription
            ? "Pagar inscripci√≥n"
            : "Confirmar y pagar"}
        </Button>
      </Form>
    </Container>
  );
}
